@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&subset=cyrillic,cyrillic-ext,latin-ext");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-build {
  width: 1.13em;
  height: 1em;
  fill: #FFA27B;
}

.icon-check {
  width: 1.3em;
  height: 1em;
  fill: #FFF;
}

.icon-home {
  width: 1.36em;
  height: 1em;
  fill: #FFCCA5;
}

body {
  background: #FFF;
  font-family: 'Open Sans', ssans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.4;
}

.out {
  display: flex;
}

@media only screen and (max-width: 1023px) {
  .out {
    flex-direction: column;
  }
}

.main,
.aside {
  overflow: auto;
  height: 100vh;
}

@media only screen and (max-width: 1023px) {
  .main,
  .aside {
    height: auto;
  }
}

.main {
  flex-grow: 1;
  padding: 80px 0 40px;
  background-image: linear-gradient(to bottom, #47457e, #2e2d54);
}

@media only screen and (max-width: 1023px) {
  .main {
    padding: 40px 0;
  }
}

.inner {
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 845px;
}

.aside {
  width: 413px;
  padding: 40px;
}

@media only screen and (max-width: 1023px) {
  .aside {
    width: 100%;
    padding: 40px 20px;
  }
}

.steps {
  margin: 0 auto;
  max-width: 589px;
  height: 24px;
  position: relative;
}

.steps__controls {
  position: relative;
  margin: 0 12px;
  height: 100%;
}

.steps__control {
  position: absolute;
  top: 0;
  width: 24px;
  height: 24px;
  padding: 6px;
  transform: translate3d(-50%, 0, 0);
}

.steps__control:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #2d2c54;
}

.steps__control[data-steps-control="1"] {
  left: 0;
}

.steps__control[data-steps-control="2"] {
  left: 33.33%;
}

.steps__control[data-steps-control="3"] {
  left: 66.66%;
}

.steps__control[data-steps-control="4"] {
  left: 100%;
}

.steps__circle {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  display: block;
  height: 100%;
  border-radius: 50%;
  background-color: #ffffff;
  text-align: center;
  font-size: 0;
}

.steps__circle:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.steps__icon {
  display: inline-block;
  vertical-align: middle;
  font-size: 10px;
  fill: #59c78a;
  transform: translateY(1px);
}

.steps__lines {
  position: absolute;
  left: 12px;
  top: 0;
  right: 12px;
  bottom: 0;
  font-size: 0;
}

.steps__line {
  position: absolute;
  top: 50%;
  margin-top: -2px;
  width: 33.33%;
  height: 3px;
  background: black;
}

.steps__line[data-steps-line="1"] {
  left: 0;
}

.steps__line[data-steps-line="2"] {
  left: 33.33%;
}

.steps__line[data-steps-line="3"] {
  left: 66.66%;
}

.pickup {
  padding: 24px;
  box-shadow: 0 0px 20px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.pickup__row {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  align-items: center;
}

.pickup__title {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.71;
  letter-spacing: 1.2px;
  color: #32325d;
}
