body {
	background: #FFF;
	font-family: 'Open Sans', ssans-serif;
	@include f(15px, 400, 1.4); }

.out {
	display: flex;
	@include md {
		flex-direction: column; } }
.main,
.aside {
	overflow: auto;
	height: 100vh;
	@include md {
		height: auto; } }
.main {
	flex-grow: 1;
	padding: 80px 0 40px;
	@include gb($twilight, $dark-grey-blue);
	@include md {
		padding: 40px 0; } }
.inner {
	position: relative;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;
	max-width: 845px; }
